/* Custom status badge classes */
.status-new {
  background-color: var(--status-new-300);
  border: 1px solid var(--status-new-500);
}

.status-pending {
  background-color: var(--status-pending-300);
  border: 1px solid var(--status-pending-500);
}

.status-active {
  background-color: var(--status-active-300);
  border: 1px solid var(--status-active-500);
}

.status-error {
  background-color: var(--error-300);
  border: 1px solid var(--error-500);
}

.status-neutral {
  background-color: var(--neutral-300);
  border: 1px solid var(--neutral-500);
}


/* Light theme override (for manual theme control) */
[data-theme="light"] {
  --background: #efefec; /* Hintergrund */
  --foreground: #000000; /* Mengentext */
  --background-primary: #efefec;
  --background-secondary: #ffffff; /* Print/over photos */
  --background-tertiary: #f5f5f2;
  --foreground-primary: #000000;
  --foreground-secondary: #333333;
  --foreground-tertiary: #666666;
  --border-primary: #e5e5e5;
  --border-secondary: #d4d4d4;
  --border-light: #f5f5f5;
  --shadow-light: rgba(0, 0, 0, 0.05);
  --shadow-medium: rgba(0, 0, 0, 0.1);
  --shadow-dark: rgba(0, 0, 0, 0.15);
}



/* Apply Plus Jakarta Sans to all elements and smooth transitions for theme changes */
* {
  font-family: "Plus Jakarta Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif !important;
  transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: "Plus Jakarta Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

